<template>
  <div>
    <div class="card virtual" :class="{'is-cancelled': card.cardStatus === 'canceled'}">
      <img v-if="cardImg" :src="cardImg" class="card-image" alt="Card">
      <div v-else class="card__default" />
      <div class="card__info">
        <div>
          <p class="card-type">
            {{
              $t('cardType_virtual')
            }}
          </p>
          <p class="card-limit-use">
            {{ card.cardStatus === 'canceled' ? 'Cancelled': cardAmount }}
          </p>
        </div>
        <div>
          <div>
            <p class="card-number">
              **** **** **** {{ card.last4 }}
            </p>
            <div class="exp-cvv">
              <div>
                <p>EXP</p>
                <span>{{ card.expiryMonth }} / {{ card.expiryYear }}</span>
              </div>
            </div>
            <p class="card-holder">
              {{ card.cardholder.name }}
            </p>
          </div>
        </div>
      </div>
      <div class="card__cancel" v-if="card.cardStatus !== 'canceled'">
        <el-button
          type="danger"
          class="close"
          @click="showCancelCardModal">
          {{ $t('cardInfo_row_title_cancel') }}
        </el-button>
      </div>
    </div>
  </div>
</template>
<script>
import { mapActions, mapGetters } from 'vuex';
import { cardStatus, cardType } from '../enums/index';
import { currencyFormatter } from '@/utils/general';

export default {
  name: 'SentCard',
  props: {
    // eslint-disable-next-line
    card: Object
  },
  data() {
    return {
      cardStatus,
      cardType,
    };
  },
  computed: {
    ...mapGetters('user', ['getProgramDetails']),
    ...mapGetters('account', ['getSelectedAccount']),
    cardImg() {
      const programDetails = this.getProgramDetails;
      if (this.card.cardType == 'physical') {
        return programDetails &&
          programDetails.brand &&
          programDetails.brand.cardArtPhysical
          ? programDetails.brand.cardArtPhysical
          : null;
      } else {
        return programDetails &&
          programDetails.brand &&
          programDetails.brand.cardArtVirtual
          ? programDetails.brand.cardArtVirtual
          : null;
      }
    },
    cardAmount() {
      return currencyFormatter(this.card.availableLimit);
    },
  },
  methods: {
    ...mapActions('card', ['cancelSentCard', 'listSentCardsByAccount']),
    cancelCard() {
      const loader = this.showLoader();
      this.cancelSentCard(this.card.id)
        .then(() => {
          this.$notify({
            title: 'Success',
            message: this.$t('sent_card_cancel_message'),
            type: 'success'
          });
          this.$emit('refresh');
        })
        .catch((e) => {
          this.apiErrorMessage(e);
        })
        .finally(()=>{
          loader.close();
        });
    },
    showCancelCardModal() {
      this.$confirm(
        this.$t('sent_card_alert_message'),
        this.$t('cardInfo_row_title_cancel'),
        {
          confirmButtonText: 'Yes',
          cancelButtonText: 'No'
        }
      )
        .then(() => {
          this.cancelCard();
        })
        .catch(() => {});
    }
  }
};
</script>
