<template>
  <div class="widgetContainer cardList full-width">
    <div class="widgetContainer__header-fixed">
      <div class="headerExport">
        <p class="header-1">
          {{ $t('sent_card') }}
        </p>
        <div v-if="false">
          <span class="icon-icon-export" />
          <p>
            {{ $t('export') }}
          </p>
          <span class="icon-arrow-right" />
        </div>
      </div>
      
      <div :class="'cardList__search ' + (cards.length !== 0 ? 'search-add': '')">
        <div class="searchInput">
          <el-input
            type="text"
            v-model="search"
            :placeholder="$t('card_search_placeholder')" clearable />
          <span class="icon-icon-search" />
        </div>
      </div>
    </div>
    
    <SkeletonCreditCard class="cardList__skeleton" :count="3" v-if="loading && page === 1" />

    <div class="widgetContainer__body widgetContainer--scrollable" v-else>
      <div class="cardList__list" v-infinite-scroll="load"
           :infinite-scroll-disabled="disabledLoadMore"
           :infinite-scroll-immediate="false">
        <ul class="cardList__dashboard-list" :class="{'has-cards': cards.length}">
          <li class="new-card" @click="showSendcard = true">
            <div class="new-card__inside">
              <div>
                <i class="el-icon-circle-plus-outline" />
                <h5>{{ $t('send_a_visa_vard') }}</h5>
              </div>
            </div>
          </li>
          <li v-for="card in cards" :key="card.id">
            <SentCard
              :show-logo="false"
              :card="card.card"
              @refresh="refresh"
              class="card-detail" />
          </li>
        </ul>
      </div>
      <p v-if="loading && page !== 1">
        Loading...
      </p>
    </div>
    <Drawer class="default-drawer" @refresh="refresh" :title="$t('send_a_visa_vard')" :components="sendCard" :show.sync="showSendcard" v-if="showSendcard" />
  </div>
</template>
<script>
import { mapActions, mapGetters } from 'vuex';
import CardMixin from '../mixins/card';
import SentCard from '../components/SentCard';
import { cardStatus } from '../enums';
import Drawer from '@/components/Drawer.vue';
import SkeletonCreditCard from '@/components/Skeleton/SkeletonCreditCard.vue';
export default {
  name: 'SentCardList',
  components: {
    SentCard,
    Drawer,
    SkeletonCreditCard,
  },
  mixins: [CardMixin],
  data() {
    return {
      search: '',
      loading: true,
      page: 1,
      limit: 25,
      showSendcard: false,
      sendCard:[
        {
          name: 'sendCard-contacts',
          default: true,
          component: () => import('@m/card/components/Contacts.vue')
        },
        {
          name: 'sendCard-form',
          component: () => import('@m/pay/views/CardPayment.vue')
        },
        {
          name: 'pay-success',
          data: {
            page: 'send-card'
          },
          component: () => import('@m/pay/views/PaySuccess.vue')
        },
        {
          name: 'sendCard-newContact',
          data: {
            page: 'send-card'
          },
          component: () => import('@m/contact/views/ContactDetailForm.vue')
        },
        {
          name: 'contact-success',
          data: {
            page: 'send-card'
          },
          component: () => import('@m/contact/views/ContactCreatedSuccess.vue')
        },
      ]
    };
  },
  computed: {
    ...mapGetters('card', ['getSentCards']),
    cards() {
      /** @type {import('@m/card/store/state').cardState['sentCards']} */
      const cards = this.getSentCards.data;
      if (this.search === '') {
        return this.getSentCards.data.filter((c) =>
          c.card !== null
        );
      }

      return cards.filter((c) =>
        c.card !== null && c.card.cardholder.name.toLowerCase().includes(this.search.toLowerCase())
      );
    },
    disabledLoadMore() {
      return (
        this.loading === true ||
        this.getSentCards.total <= this.page * this.limit
      );
    }
  },
  created() {
    this.getData();
  },
  methods: {
    ...mapActions('card', ['listSentCardsByAccount']),
    status(c) {
      return cardStatus[c.cardStatus];
    },
    getData() {
      this.loading = true;

      this.listSentCardsByAccount({
        accountId: this.getSelectedAccount.id,
        limit: this.limit,
        offset: (this.page - 1) * this.limit
      })
        .catch((e) => {
          console.log(e);
          this.apiErrorMessage(e);
        })
        .finally(() => {
          this.loading = false;
        });
    },
    load() {
      if (
        this.loading === false &&
        this.getSentCards.total > this.page * this.limit
      ) {
        this.page++;
        this.getData();
      }
    },
    createCard() {
      this.newCard();
    },
    refresh() {
      this.page = 1;
      this.getData();
    }
  },
  watch: {
    /**
     * selected account listener
     */
    getSelectedAccount(newVal, oldVal) {
      if(newVal && oldVal && newVal.id !== oldVal.id) {
        this.page = 1;
        this.getData();
      }
    }
  }
};
</script>

